import './App.css';
import React from 'react'
import Todo from './Todo';
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { 
      taskName: '',
      level : 0,
      ArrayTask : [],
      done : false
  };
}

handleChange = (event) => {
  this.setState({taskName: event.target.value})
}

  
handleKeyPress = (e) =>{
  if(e.key === 'Enter'){
    if( this.state.taskName === ''){
      return;
    }
    const id = this.state.ArrayTask.length;
    const name = this.state.taskName;
    const done = this.state.done;
    this.state.ArrayTask.push({id,name,done: done,level: 0} )
    this.setState({taskName: ''})
    e.preventDefault()
  }
}

addTask = () => {
  if( this.state.taskName === ''){
    return;
  }

    const id = this.state.ArrayTask.length;
    const name = this.state.taskName;
    const done = this.state.done;
    this.state.ArrayTask.push({id,name,done: done,level: 0} )
    this.setState({taskName: '',done: false})
}

handleDelete = (id) => {
  const tasks = this.state.ArrayTask.filter((element)=> element.id !== id)
  this.setState({ArrayTask: tasks})

}
handlePriority = (id,lev) => {
  const tasks = this.state.ArrayTask.map((e)=>{
    if(e.id === id){
      e.level = lev;
    }
    return e;
  })
  this.setState({ArrayTask: tasks})
  this.handleSortdefault()
   

}

 handleSortdefault = () =>{
  let TasksSorted = this.state.ArrayTask.sort((a, b) => b.level - a.level)
   this.setState({ArrayTask : TasksSorted})
}

handleUpdate = (textUpdated,id) =>{
  const items = this.state.ArrayTask;
  items.map((item)=>{
    if(item.id === id){
      item.name = textUpdated;
    }
    return item;
  })
  this.setState({ArrayTask: items})

}

  render(){

  return (
  <div className="container mx-auto">
       <h1>To-Do List</h1>
        <form  className="add text-center my-4">
              <input type="text" value={this.state.taskName}  className="form-control m-auto" name="content" onChange={this.handleChange} onKeyPress={this.handleKeyPress} placeholder="Add new Task..."/>
              <i className="fa fa-plus right-input " aria-hidden="true" onClick={this.addTask} ></i>
              
          </form>
        <div className="items"> 
      
         {this.state.ArrayTask.map((element)=>{
          return (
            <Todo 
            key={element.id}
      id={element.id} 
      value={element.name} 
      done={element.done}
      level={element.level}
      handleDelete={this.handleDelete}
      handlePriority={this.handlePriority}
      handleSortdefault={this.handleSortdefault}
      handleUpdate = {this.handleUpdate}
            />
          )
         })}
         
         
          <h2 className="done" aria-hidden="true">Completed</h2>
          <h2 className="undone" aria-hidden="true"></h2>
        </div>
</div>
  );
}
}

export default App;
