import './Todo.css';
import React from 'react'
 
class Todo extends React.Component {
  constructor(props) {
    super(props);
    this.state = { id:props.id , value:props.value, done:props.done,level:1};
  }


handlePriority = (lev,id) => {
  if(lev === 0){
    this.setState({level: 1})
  }
  else if(lev === 1){
    this.setState({level: 0})
  }
  console.log('todo: ',this.state.level);
 this.props.handlePriority(id,this.state.level);
 
  
}

handleDone = (d) =>{
  this.setState({done: !d})
}


  render() {

let checkPriority = '';
if ( this.props.level){
  checkPriority = 'fa fa-star loca ';
}
else {
  checkPriority = 'fa fa-star-o loca ';
}

let checkDone = ''
let Completed = '';
let textdecor = '';
if (this.state.done){
  checkDone = 'fa fa-check-circle left   ';
  Completed = "completed ";
  textdecor = "edit decor";
}
else {
  checkDone = 'fa fa-circle-thin left ';
  Completed = '';
  textdecor = 'edit';
}




    return (    
        <React.Fragment>
         {/* <input id={this.props.id}  /> */}
         
         <label className={Completed} for={this.props.id}>
         <input className={textdecor} type="text" value={this.props.value} onChange={(e) =>{this.props.handleUpdate(e.target.value,this.state.id)}} name="content"/>
         <i onClick={()=>this.handleDone(this.state.done)} className={checkDone} aria-hidden="true"></i>
         <i className={checkPriority} onClick={()=> this.handlePriority(this.state.level,this.state.id)} aria-hidden="true"></i>
         <i onClick={()=> this.props.handleDelete(this.props.id) } className="fa fa-times" aria-hidden="true"></i>
         </label>
         </React.Fragment>
    );

  }
}



export default Todo;
